








































.icon {
  vertical-align: middle;
}
.svg-icon {
  stroke-width: 1 !important;
  width: 1.125rem;
  height: 1.125rem;
  vertical-align: middle;

  &.svg-icon--red-check,
  &.svg-icon--green-check {
    height: 0.75rem;
  }
  &.svg-icon--red-arrow,
  &.svg-icon--green-arrow {
    height: 0.5625rem;
  }
  &.svg-icon--plus {
    height: 1rem;
  }
  &.svg-icon--file-upload {
    width: 100%;
    height: 100%;
  }
}
.button.is-primary.is-small {
  min-width: 1.825rem;
}
.button.is-primary.is-small .svg-icon {
  width: 1rem;
  height: 1rem;
}
